// src/contexts/AppModalContext.tsx
"use client";

import React, { createContext, useCallback, useState } from "react";
import WaterModal from "@/components/modals/WaterModal";

type ModalState = {
  open: boolean;
  data?: any;
  onSaved?: () => void;
};

type AppModalContextType = {
  openWater: (opts?: { data?: any; onSaved?: () => void }) => void;
  close: () => void;
};

export const AppModalContext = createContext<AppModalContextType>({
  openWater: () => {},
  close: () => {},
});

export default function AppModalProvider({ children }: { children: React.ReactNode }) {
  const [waterModal, setWaterModal] = useState<ModalState>({ open: false });

  const openWater = useCallback((opts?: { data?: any; onSaved?: () => void }) => {
    setWaterModal({ open: true, data: opts?.data, onSaved: opts?.onSaved });
  }, []);

  const close = useCallback(() => {
    setWaterModal({ open: false, data: undefined, onSaved: undefined });
  }, []);

  return (
    <AppModalContext.Provider value={{ openWater, close }}>
      {children}

      {/* Render modal(s) here */}
      <WaterModal
        open={waterModal.open}
        data={waterModal.data}
        onClose={() => {
          setWaterModal({ open: false });
        }}
        onSaved={() => {
          // chama callback externo (ex.: recarregar metas)
          if (waterModal.onSaved) waterModal.onSaved();
          setWaterModal({ open: false });
        }}
      />
    </AppModalContext.Provider>
  );
}
